import React from "react";
import dayjs from "dayjs";
import classNames from "classnames";
import { observer, inject } from "mobx-react";
import { DatePicker, Icon, SearchBar, Modal, List, Button, Checkbox, Toast } from "antd-mobile";
import SubPage from "@/components/SubPage";
import { toJS } from "mobx";
import storage from "@/utils/storage";
require("./style.scss");

const CheckboxItem = Checkbox.CheckboxItem;

interface IProps {
  [key: string]: any;
}

interface IState {
  [key: string]: any;
}

@inject("SelectMeetingRoomStore")
@observer
class SelectMeetingRoom extends React.Component<IProps, IState> {
  componentDidMount() {
    const {
      location: { state },
      SelectMeetingRoomStore: { fetchData, initCurMeeting }
    } = this.props;
    fetchData();
    initCurMeeting(state);
  }

  componentWillUnmount() {
    const {
      SelectMeetingRoomStore: { reset }
    } = this.props;
    reset();
  }

  submitTime = () => {
    const {
      SelectMeetingRoomStore: { date, cycle, curSelectMeeting, onVisibleChange }
    } = this.props;

    let times: any[] = [];
    let isContinuation = true;
    let pointer;

    for (let i = 0; i < cycle.length; i++) {
      if (cycle[i].checked) {
        if ((pointer || pointer === 0) && i - 1 !== pointer) {
          isContinuation = false;
          break;
        }
        pointer = i;
        times.push({ start: cycle[i].start, end: cycle[i].end });
      }
    }

    if (isContinuation) {
      this.props.history.goBack();

      let endTime = dayjs(`${dayjs(date).format("YYYY-MM-DD")} ${times[times.length - 1].end}`);

      if (endTime.isSame(dayjs(date).startOf("day"))) {
        endTime = dayjs(date).add(1, "day").startOf("day");
      }

      storage.setItem(
        "selectMeetingRoom",
        JSON.stringify({
          id: curSelectMeeting.id,
          name: curSelectMeeting.name,
          remarks: curSelectMeeting.remarks,
          number: curSelectMeeting.roomCapacity,
          startTime: dayjs(`${dayjs(date).format("YYYY-MM-DD")} ${times[0].start}`).format(),
          endTime: endTime.format()
        })
      );
      // onVisibleChange(false);
    } else {
      Toast.info("请选择连续的时间");
    }
  };

  render() {
    const {
      history,
      SelectMeetingRoomStore: {
        week,
        title,
        dayline,
        hourNumber,
        cycle,
        criticalPoint,
        searchText,
        onSearchTextChange,
        visible,
        onVisibleChange,
        date,
        onDateChange,
        curSelectMeeting,
        onSelectMeeting,
        data,
        onCheckboxItemChange
      }
    } = this.props;

    return (
      <SubPage history={history} className="meeting-selectRoom-page" title={title} bgColor="#eee">
        <div>
          <div className="meeting-selectRoom-header">
            <DatePicker mode="date" title="选择日期" minDate={dayjs().toDate()} value={dayjs(date).toDate()} onChange={onDateChange}>
              <div className="meeting-selectRoom-selectDate">
                <span className="meeting-selectRoom-timeIcon"></span>
                <span className="meeting-selectRoom-text">{`${dayjs(date).format("MM月DD日")} 周${week[dayjs(date).day()]}`}</span>
                <Icon type="down" size="sm" />
              </div>
            </DatePicker>
            <SearchBar
              className="meeting-selectRoom-search-bar"
              placeholder="搜索会议室"
              onSubmit={onSearchTextChange}
              onClear={() => {
                onSearchTextChange("");
              }}
            />
          </div>
          <div>
            {data
              .filter((item: any) => !searchText || (item.name && item.name.indexOf(searchText) !== -1))
              .map((item: any, i: number) => (
                <div
                  key={item.id || i}
                  className="meeting-selectRoom-Timeline"
                  onClick={() => {
                    onSelectMeeting(item);
                    onVisibleChange(true);
                  }}
                >
                  <div className="meeting-selectRoom-Timeline-title">
                    {item.name}（{item.number || 0}人）
                  </div>
                  <div className="meeting-selectRoom-Timeline-content">
                    <div className="meeting-selectRoom-Timeline-line">
                      {(() => {
                        return dayline.map((v: any, i: number) => {
                          return (
                            <span
                              key={i}
                              className={classNames({
                                "meeting-selectRoom-Timeline-line_use": item.useLine.indexOf(i + 14) !== -1,
                                "meeting-selectRoom-Timeline-line_expire": i + 14 < criticalPoint
                              })}
                            ></span>
                          );
                        });
                      })()}
                    </div>
                    <div className="meeting-selectRoom-Timeline-number">
                      {hourNumber.map((v: any, i: number) => (
                        <span key={i}>{i + 7}</span>
                      ))}
                    </div>
                  </div>
                </div>
              ))}
          </div>
        </div>
        <Modal
          popup
          visible={visible}
          onClose={() => {
            onVisibleChange(false);
          }}
          animationType="slide-up"
          maskClosable
        >
          {(() => {
            return (
              <List
                renderHeader={() => (
                  <div>
                    {curSelectMeeting.name}（{curSelectMeeting.number}人）
                  </div>
                )}
                className="popup-list"
              >
                <div className="meeting-selectRoom-selectMeeting">
                  {cycle.map((item: any, i: number) => {
                    return (
                      <CheckboxItem
                        key={i}
                        checked={item.checked}
                        onChange={(e: any) => {
                          onCheckboxItemChange(item, i, e.target.checked);
                        }}
                        disabled={(item.isExpire || item.isUse) && !item.isCurMeeting}
                      >
                        <div className="meeting-selectRoom-checkbox">
                          <span>{item.label}</span>
                          {item.isExpire ? <span>已过期</span> : item.isUse ? <span>已被{`${item.name || ""}`}预订</span> : <span></span>}
                        </div>
                      </CheckboxItem>
                    );
                  })}
                </div>
                <div className="meeting-selectRoom-selectMeeting-handle">
                  <Button type="primary" onClick={this.submitTime}>
                    立即预订
                  </Button>
                </div>
              </List>
            );
          })()}
        </Modal>
      </SubPage>
    );
  }
}

export default SelectMeetingRoom;
